import React from 'react'
import {useState, useEffect} from 'react'
import PlayerPanel from './PlayerPanel'
import './RoundPage.css'

const RoundPage = (props) => {

    //Game phases
    const data = props.data
    console.log(data);
    const [gamemode, setGamemode] = useState(data.gamemode)

    useEffect(()=>{
        console.log('use effect ran');
    })

    return (
        <div className='view round-view'>
            <div className="round-info">
                <div><span>Round #</span><span>Instructions</span></div>
                <div><span>Cards: &lt; # &gt;</span><span>Remaining #</span></div>
            </div>

            <div className="playerPanels">
                {data.players.length >= 2 ? (
                    showPlayers()
                ) : (
                    noPlayers()
                )}
            </div>
            
            <div className="buttonPanel">
                <div className={gamemode == 'set' ? 'btn btn-active' : 'btn btn-disabled'} onClick={gamemode == 'set' ? setBets : undefined}><span>Set</span></div>
                <span>&gt;</span>
                <div className={gamemode == 'play' ? 'btn btn-active' : 'btn btn-disabled'}><span>Play</span></div>
                <span>&gt;</span>
                <div className={gamemode == 'end' ? 'btn btn-active' : 'btn btn-disabled'}><span>End</span></div>
                <span>&gt;</span>
                <div className={gamemode == 'next' ? 'btn btn-active' : 'btn btn-disabled'}><span>Next</span></div>
            </div>
        </div>
    )

    function noPlayers(){
        return <h2>Please add some players first.</h2>
    }
    function showPlayers(){
        return data.players.map((player)=>{
            return <PlayerPanel key={player.id} player={player} gamemode={gamemode}/>
        })
    }
    function setBets(){
        data.players.map(player=>player.setBet())
        data.gamemode = 'play';
        props.setData(data);
        setGamemode('play')
    }
}

export default RoundPage
