import Navbar from './Navbar'
import {HashRouter as Router, Route, Switch} from 'react-router-dom'
import SetupPage from './SetupPage'
import RoundPage from './RoundPage'
import ScoreboardPage from './ScoreboardPage'
import SettingsPage from './SettingsPage'
import {useState} from 'react'
import './Mainscreen.css'

const Mainscreen = () => {
    /*DUMMYDATA*/ 
    const tempPlayers = [];
    tempPlayers.push(new Player("Harley", tempPlayers.length+1))
    tempPlayers.push(new Player("Tim", tempPlayers.length+1))
    tempPlayers.push(new Player("Kevin", tempPlayers.length+1))
    const [players, setPlayers] = useState(tempPlayers);
    //const [players, setPlayers] = useState([]);
    const startData = {
        gamemode: 'set',
        players: [],
        round: 1,
        roundHistory: []
    }
    startData.players = tempPlayers;
    const [data, setData] = useState(startData)





    return (
        <>
            <Router>
                <Switch>
                    <Route path='/mainscreen/setup'>
                        <Navbar title='Setup Game' />
                        <SetupPage data={data} setData={setData} player={Player}/>
                    </Route>
                    <Route path='/mainscreen/round'>
                        <Navbar title='Game Round' />
                        <RoundPage data={data} setData={setData}/>
                    </Route>
                    <Route path='/mainscreen/scoreboard'>
                        <Navbar title='Scoreboard' />
                        <ScoreboardPage data={data}/>
                    </Route>
                    <Route path='/mainscreen/settings'>
                        <Navbar title='Settings' />
                        <SettingsPage data={data}/>
                    </Route>
                </Switch>
            </Router>    
        </>
    )
}

function Player(name, id){
    this.id = id;
    this.name = name;
    this.score = 10;
    this.bet = 0;
    this.result = 0;
    this.tempBet = 0;
    this.tempResult = 0;
    this.roundHistory = []

    this.setTempBet = (bet)=>{
        this.tempBet = bet;
    }
    this.setTempResult = (result)=>{
        this.tempResult = result;
    }
    this.setBet = ()=>{
        this.bet = this.tempBet;
    }
    this.setResult = ()=>{
        this.result = this.tempResult;
    }

}



export default Mainscreen
